/* Responsive Design */
@media screen and (max-width: 768px) {
  .user-layout {
    flex-direction: column;
  }

  .admin_sidebar {
    position: fixed;
    z-index: 1000;
    height: 100%;
  }

  .layout-content {
    margin-left: 0;
  }
  .main-content {
    flex: 1;
    padding: 10px;
    overflow-y: auto;
    transition: background-color 0.3s ease, color 0.3s ease;
    color: inherit;
  }
}
